// src/components/CategoryPage.js
import React, { createContext, useState, useEffect, useContext } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { Container, Typography, Grid, Card, CardMedia, CardContent, Box, Select, MenuItem, FormControl, InputLabel, Pagination, Paper, Checkbox, Button, Modal,IconButton } from '@mui/material';
import axios from 'axios';
import { useLocation } from 'react-router-dom';
import { Link } from 'react-router-dom';  // Asegrate de importar Link
import { Breadcrumbs } from '@mui/material';
import config from '../config';
import ProductList from './ProductList';
import { ModelosContext } from '../contexts/ModelosContext';
import { initialMarcas } from '../constants';
import MenuIcon from '@mui/icons-material/Menu';
import Collapse from '@mui/material/Collapse';


// Funcin para capitalizar las rutas (opcional)
const capitalize = (str) => str.charAt(0).toUpperCase() + str.slice(1);

function CategoryPage() {
    const { categoryName = '' } = useParams(); // Inicializa categoryName como cadena vaca si est indefinido
    const navigate = useNavigate();
    const [productos, setProductos] = useState([]);
    const [page, setPage] = useState(1);
    const productsPerPage = 20;
    const [sortOrder, setSortOrder] = useState('');
    const [filter, setFilter] = useState('');
    const [compareMode, setCompareMode] = useState(false);
    const [selectedProducts, setSelectedProducts] = useState([]);
    const [openModal, setOpenModal] = useState(false);
    const location = useLocation();
    const [brandFilter, setBrandFilter] = useState('');
    const [modelFilter, setModelFilter] = useState('');
    const [discountFilter, setDiscountFilter] = useState('');
    const [storeFilter, setStoreFilter] = useState('');
    const [marcas, setMarcas] = useState([]); // Lista de marcas disponibles
    const [modelos, setModelos] = useState([]); // Lista de modelos especficos segn la marca seleccionada
    const [categoryFilter, setCategoryFilter] = useState(''); // Nuevo estado para manejar la categora seleccionada
    const [errorMessage, setErrorMessage] = useState(null);
    const { modelosDisponibles, setModelosDisponibles } = useContext(ModelosContext);
    const imageUrl = location.state?.imageUrl || 'URL_DE_IMAGEN_POR_DEFECTO';
    const [filtersOpen, setFiltersOpen] = useState(false); // Estado para el colapso
    const [open, setOpen] = useState(false);

    //NUEVO
    const handleBrandChange = (event) => {
        const selectedBrand = event.target.value;
        setBrandFilter(selectedBrand);
        setModelFilter(''); // Reiniciar el modelo cuando cambia la marca
        fetchModelosPorMarca(selectedBrand); // Cargar modelos segn la marca seleccionada
        applyFilters(); // Aplicar filtros despus de seleccionar la marca
    };
    
    const handleModelChange = (event) => {
        setModelFilter(event.target.value);
        applyFilters(); // Llamar a `applyFilters` cada vez que cambie el filtro de modelo
    };
    const handleDiscountChange = (event) => setDiscountFilter(event.target.value);
    const handleStoreChange = (event) => setStoreFilter(event.target.value);
    // Estado adicional para el filtro de categora

    // Funcin para manejar el cambio de categora en el filtro
    const handleCategoryChange = (event) => {
        setCategoryFilter(event.target.value);  // Actualiza el estado local con la nueva categora seleccionada
    };


    // Nueva funcin para manejar la carga de modelos basados en la marca seleccionada
    const fetchModelosPorMarca = async (marca) => {
        try {
            const response = await axios.get(`${config.apiBaseUrl}/modelos`, { params: { marca } });
            setModelosDisponibles(response.data); // Cargar los modelos disponibles para la marca seleccionada
        } catch (error) {
            console.error("Error al obtener modelos:", error);
            setModelosDisponibles([]); // En caso de error, establecer modelos como vaco
        }
    };
    
    //const handleBrandChange = (event) => setBrandFilter(event.target.value);
    //const handleModelChange = (event) => setModelFilter(event.target.value);
    //const handleDiscountChange = (event) => setDiscountFilter(event.target.value);
    //const handleStoreChange = (event) => setStoreFilter(event.target.value);

    //Funcin para restablecer filtros
    const resetFilters = () => {
        setBrandFilter('');
        setModelFilter('');
        setDiscountFilter('');
        setStoreFilter('');
        setCategoryFilter('');
        setSortOrder('');
        setErrorMessage(null);
        
        // Llama a la funcin inicial de carga de productos basada en la categora o marca en la URL
        fetchInitialProducts();
    };



    
    
    
    // useEffect para la carga inicial basada en la URL (marca o categora)
    useEffect(() => {
        setModelFilter('');  // Limpiar filtro de modelo
        setDiscountFilter('');  // Limpiar filtro de descuento
        setStoreFilter('');  // Limpiar filtro de tienda
        console.log("Ruta actual:", location.pathname);
        console.log("Valor de categoryName:", categoryName);

        if (location.pathname.includes('/marca/')) {
            const brandFromPath = location.pathname.split('/marca/')[1]; // Extrae la marca de la URL
            console.log("Marca detectada en la ruta:", brandFromPath);
            setBrandFilter(categoryName); // Decodifica si hay caracteres especiales
            fetchProductosPorMarca(categoryName);
        } else if (location.pathname.includes('/categoria/')) {
            setCategoryFilter('');  // Limpiar filtro de categora
            setCategoryFilter(categoryName); // Configura el filtro de categora inicial
            fetchProductosPorCategoria(categoryName); // Bsqueda inicial por categora
        }
    }, [categoryName, location.pathname]);

    // useEffect para aplicar filtros cuando se cambian
    useEffect(() => {
        if (brandFilter || modelFilter || discountFilter || storeFilter || filter || sortOrder) {
            applyFilters();  // Aplica los filtros activos
        }
    }, [brandFilter, modelFilter, discountFilter, storeFilter, filter, sortOrder]);
    
    // useEffect para cargar los modelos basados en la marca seleccionada
    useEffect(() => {
        if (brandFilter) {
            fetchModelosPorMarca(brandFilter);  // Cargar modelos si hay una marca seleccionada
        } else {
            setModelos([]);  // Limpiar modelos si no hay marca seleccionada
        }
    }, [brandFilter]);
    
    useEffect(() => {
        if (categoryFilter || brandFilter || modelFilter || discountFilter || storeFilter || sortOrder) {
            applyFilters();  // Aplica los filtros automticamente cuando cambia `categoryFilter`
        }
    }, [categoryFilter, brandFilter, modelFilter, discountFilter, storeFilter, sortOrder]);
    

    
    const pathnames = location.pathname.split('/').filter((x) => x);

    //Para los filtros de se aplican desde el home
    useEffect(() => {
        const queryParams = new URLSearchParams(location.search);
    
        const marca = queryParams.get('marca') || '';
        const modelo = queryParams.get('modelo') || '';
        const categoria = queryParams.get('categoria') || '';
        const searchParams = new URLSearchParams();
        if (selectedFilters.marca) searchParams.set('marca', selectedFilters.marca);
        if (selectedFilters.modelo) searchParams.set('modelo', selectedFilters.modelo);
        if (selectedFilters.categoria) searchParams.set('categoria', selectedFilters.categoria);
        
        setBrandFilter(marca);
        setModelFilter(modelo);
        setCategoryFilter(categoria);
    
        if (marca || modelo || categoria) {
            applyFilters(); // Solo aplica si hay filtros vlidos
        }    }, [location.search]);


    const [selectedFilters, setSelectedFilters] = useState({
        marca: '',
        modelo: '',
        categoria: '',
    });
    
    //Importamos las marcas desde mi constants.js
    useEffect(() => {
        setMarcas(initialMarcas);
    }, []);



    
    
    //CODIGO NUEVO
    const fetchProducts = () => {
        // Determinar si se est haciendo una bsqueda por marca o por categora
        const searchType = location.pathname.includes('/marca/') ? 'marca' : 'categoria';
    
        axios.get(`${config.apiBaseUrl}`, {
            params: {
                [searchType]: categoryName, // Buscar por categora o marca, segn corresponda
                marca: brandFilter || undefined,
                modelo: modelFilter || undefined,
                descuento: discountFilter || undefined,
                tienda: storeFilter || undefined,
                sortOrder: sortOrder || undefined,
            }
        })
        .then(response => {
            console.log("Productos recibidos:", response.data);
            setProductos(response.data);
            if (response.data.length === 0) {
                setErrorMessage("No se encontraron productos que coincidan con la bsqueda.");
            }
        })
        .catch(error => {
            console.error("Error al obtener productos:", error);
            setErrorMessage(error.response?.data?.message || "Error al obtener productos de la API.");
        });
    };
    
    const fetchInitialProducts = () => {
        if (location.pathname.includes('/marca/')) {
            fetchProductosPorMarca(categoryName); // Si es bsqueda por marca
        } else if (location.pathname.includes('/categoria/')) {
            setCategoryFilter(categoryName); // Configura el filtro de categora inicial
            fetchProductosPorCategoria(categoryName); // Si es bsqueda por categora
        } else {
            applyFilters(); // Aplica los filtros en cualquier otro caso
        }
    };
    
    const applyFilters = () => {
        axios.get(`${config.apiBaseUrl}`, {
            params: {
                categoria: categoryFilter || undefined,
                marca: brandFilter || undefined,
                modelo: modelFilter || undefined,
                descuento: discountFilter || undefined,
                tienda: storeFilter || undefined,
            }
        })
        .then(response => {

            let productosOrdenados = response.data;

            // Aplica el orden en el front-end en caso de que la API no realice el ordenamiento
            if (sortOrder) {
                productosOrdenados = productosOrdenados.sort((a, b) => {
                    if (sortOrder === 'priceAsc') {
                        return a.precio_actual - b.precio_actual;
                    } else if (sortOrder === 'priceDesc') {
                        return b.precio_actual - a.precio_actual;
                    } else if (sortOrder === 'nameAsc') {
                        return a.nombre.localeCompare(b.nombre);
                    } else if (sortOrder === 'nameDesc') {
                        return b.nombre.localeCompare(a.nombre);
                    }
                    return 0;
                });
            }
            setProductos(productosOrdenados);


            if (response.data.length === 0) {
                setErrorMessage("No se encontraron productos para los filtros seleccionados.");
                setProductos([]); // Asegrate de limpiar la lista de productos
            } else {
                setErrorMessage(null); // Limpia cualquier mensaje de error si hay resultados
                setProductos(response.data);
            }
        })
        .catch(error => {
            console.error("Error al aplicar filtros:", error);
            setErrorMessage("Hubo un error al obtener los productos. Por favor, intenta de nuevo.");
        });
    };
    
    const fetchProductosPorMarca = async (nombreMarca) => {
        try {
            const response = await axios.get(`${config.apiBaseUrl}/marca`, { params: { nombre: nombreMarca } });
            setProductos(response.data);
            console.log(nombreMarca)
            if (response.data.length === 0) {
                setErrorMessage("No se encontraron productos para la marca seleccionada.");
                setProductos([]); // Limpia los productos si la respuesta est vaca
            } else {
                setErrorMessage(null); // Limpia cualquier mensaje de error si hay resultados
                setProductos(response.data);
            }
        } catch (error) {
            console.error("Error al obtener productos por marca:", error);
            setErrorMessage("Hubo un error al obtener los productos por marca. Por favor, intenta de nuevo.");
        }
    };
    
    //Seccion de filtros
    const fetchProductosPorCategoria = async (nombreCategoria) => {
        try {
            const response = await axios.get(`${config.apiBaseUrl}/categoria`, {
                params: {
                    categoria: nombreCategoria,
                    marca: brandFilter || undefined,
                    modelo: modelFilter || undefined,
                    descuento: discountFilter || undefined,
                    tienda: storeFilter || undefined,
                },
            });
            if (response.data.length === 0) {
                setErrorMessage("No se encontraron productos en esta categora.");
                setProductos([]); // Limpia los productos si la respuesta est vaca
            } else {
                setErrorMessage(null); // Limpia cualquier mensaje de error si hay resultados
                setProductos(response.data);
            }
            setProductos(response.data);
        } catch (error) {
            console.error("Error al obtener productos por categora:", error);
            setErrorMessage("No se encontraron productos en esta categora.");
        }
    };

    const handlePageChange = (event, value) => setPage(value);
    const handleSortChange = (event) => setSortOrder(event.target.value);
    const handleFilterChange = (event) => setFilter(event.target.value);



    //Seccion de comparacion de productos
    const handleSelectProduct = (producto) => {
        if (selectedProducts.includes(producto)) {
            setSelectedProducts(selectedProducts.filter((p) => p !== producto));
        } else if (selectedProducts.length < 3) {
            setSelectedProducts([...selectedProducts, producto]);
        } else {
            alert("Solo puedes comparar hasta 3 productos.");
        }
    };
    
    const handleShowComparison = () => {
        if (selectedProducts.length >= 2) { // Permite 2 o 3 productos
            setOpenModal(true);
        } else {
            alert("Selecciona al menos 2 productos para comparar.");
        }
    };
    const handleCompareToggle = () => {
        setCompareMode(!compareMode);
        setSelectedProducts([]);
    };
    const handleCloseModal = () => {
        setOpenModal(false);
    };

    const paginatedProducts = productos.slice((page - 1) * productsPerPage, page * productsPerPage);

    return (
            <Container maxWidth="lg" sx={{ mt: 1 }}>
                            {/* Seccin de fondo y filtros */}
                            <Box
                            sx={{
                                width: '100vw',
                                height: { xs: '15vh', md: '20vh' }, // Cambia la altura para dispositivos ms pequeos
                                backgroundSize: 'cover',
                                backgroundPosition: 'center',
                                backgroundRepeat: 'no-repeat',
                                position: 'relative',
                                left: '50%',
                                transform: 'translateX(-50%)',
                                display: 'flex',
                                flexDirection: 'column',
                                alignItems: 'center',
                                justifyContent: 'center',
                                textAlign: 'center',
                                color: 'white',
                                mb: 4,
                                p: 2,
                                zIndex: 2,
                            }}
                        >
                            {/* Imagen de fondo desenfocada */}
                            <Box
                                sx={{
                                    position: 'absolute',
                                    top: 0,
                                    left: 0,
                                    width: '100%',
                                    height: '100%',
                                    //backgroundImage: 'url(https://img.freepik.com/fotos-premium/sistema-frenos-disco-freno-coche-pinza-aislado-sobre-fondo-blanco_708636-433.jpg)', // Tu imagen de fondo
                                    backgroundImage: `url(${imageUrl})`,

                                    backgroundSize: 'cover',
                                    backgroundPosition: 'center',
                                    backgroundRepeat: 'no-repeat',
                                    filter: 'blur(2px)', // Ajusta el nivel de desenfoque segn prefieras
                                    zIndex: 1,
                                }}
                            />

                            {/* Overlay oscuro para mejorar la visibilidad del texto */}
                            <Box
                                sx={{
                                    position: 'absolute',
                                    top: 0,
                                    left: 0,
                                    width: '100%',
                                    height: '100%',
                                    backgroundColor: 'rgba(0, 0, 0, 0.4)', // Opacidad ajustable
                                    zIndex: 1,
                                }}
                            />
                            <Typography
                            variant="h3"
                            sx={{
                                fontWeight: 'bold',
                                mb: 2,
                                position: 'relative',
                                zIndex: 2,
                                fontSize: { xs: '1.8rem', md: '3rem' },
                            }}
                            >
                            {location.pathname.includes('/marca/') 
                                ? `Marca: ${categoryName}` 
                                : `Categora: ${categoryName}`}
                            </Typography>
                        </Box>

                {/* Breadcrumbs dinmico para la navegacin */}
                <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
                <Breadcrumbs aria-label="breadcrumb">
                    <Link color="inherit" href="/" onClick={(e) => {
                        e.preventDefault();
                        navigate('/');
                    }}>
                        Inicio
                    </Link>        
                    {location.pathname.includes('/marca/') 
                        ? <Typography color="text.primary">Marca - {categoryName}</Typography>
                        : <Typography color="text.primary">Categora - {categoryName}</Typography>}

                </Breadcrumbs>
                <FormControl variant="outlined" sx={{ minWidth: 150 }}>
                <InputLabel>Ordenar por</InputLabel>
                <Select value={sortOrder} onChange={handleSortChange} label="Ordenar por">
                    <MenuItem value="priceAsc">Precio: Menor a Mayor</MenuItem>
                    <MenuItem value="priceDesc">Precio: Mayor a Menor</MenuItem>
                    <MenuItem value="nameAsc">Nombre: A-Z</MenuItem>
                    <MenuItem value="nameDesc">Nombre: Z-A</MenuItem>
                </Select>
                </FormControl>


                </Box>



        
                {/* Contenedor en dos columnas para filtros y productos */}
                <Grid container spacing={3}>
                    {/* Columna de filtros */}
                    <Grid item xs={12} md={3}>
                    <Box sx={{ mb: 2 }}>
                        {/* Botn de hamburguesa */}
                        <IconButton 
                            onClick={() => setOpen(!open)} 
                            sx={{ display: { xs: 'block', md: 'none' } }} // Solo visible en dispositivos pequeos
                            color="primary"
                        >
                            <MenuIcon />
                        </IconButton>



                        {/* Contenedor colapsable de filtros */}
                        <Collapse in={open || window.innerWidth >= 960} timeout="auto" unmountOnExit>
                        
                        <Paper elevation={3} sx={{ p: 2, bgcolor: '#f9black9f9' }}>
                            {/* Encabezado de Filtros con enlace para limpiar */}
                            <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
                                <Typography variant="h6">Filtros</Typography>
                                <Typography
                                variant="body2"
                                color="primary"
                                sx={{ cursor: 'pointer', textDecoration: 'underline' }}
                                onClick={resetFilters}
                                >
                                Limpiar Filtros
                                </Typography>
                            </Box>

                            {/* Filtro por Marca */}
                            <FormControl variant="outlined" fullWidth sx={{ mb: 2 }}>
                                <InputLabel>Marca</InputLabel>
                                    <Select value={brandFilter} onChange={(event) => setBrandFilter(event.target.value)} label="Marca">
                                <MenuItem value="">Ninguno</MenuItem>
                                        {marcas && marcas.length > 0 ? (
                                            marcas.map((marca, index) => (
                                    <MenuItem key={index} value={marca}>
                                    {marca}
                                    </MenuItem>
                                            ))
                                        ) : (
                                            <MenuItem disabled>Cargando marcas...</MenuItem>
                                        )}
                                    </Select>
                            </FormControl>
                            
                            {/* Filtro por modelo */}
                            <FormControl variant="outlined" fullWidth sx={{ mb: 2 }}>
                                <InputLabel>Modelo</InputLabel>
                                <Select value={modelFilter} onChange={(event) => setModelFilter(event.target.value)} label="Modelo">
                                <MenuItem value="">Ninguno</MenuItem> {/* Aade una opcin vaca */}
                                {modelosDisponibles && modelosDisponibles.length > 0 && modelosDisponibles.map((modelo, index) => (
                                    <MenuItem key={index} value={modelo}>{modelo}</MenuItem>
                                ))}
                                </Select>
                            </FormControl>

                            {/* Filtro por descuento */}
                            <FormControl variant="outlined" fullWidth sx={{ mb: 2 }}>
                                <InputLabel>Descuento</InputLabel>
                                <Select value={discountFilter} onChange={handleDiscountChange} label="Descuento">
                                    <MenuItem value={10}>10% o ms</MenuItem>
                                    <MenuItem value={20}>20% o ms</MenuItem>
                                    <MenuItem value={30}>30% o ms</MenuItem>
                                    {/* Agrega ms niveles de descuento aqu */}
                                    <MenuItem value="">Ninguno</MenuItem> {/* Aade una opcin vaca */}
                                    {modelosDisponibles.map((modelo) => (
                                        <MenuItem key={modelo} value={modelo}>{modelo}</MenuItem>
                                    ))}
                                </Select>
                            </FormControl>
        
                            {/* Filtro por tienda */}
                            <FormControl variant="outlined" fullWidth sx={{ mb: 2 }}>
                                <InputLabel>Tienda</InputLabel>
                                <Select value={storeFilter} onChange={handleStoreChange} label="Tienda">
                                    <MenuItem value="Paris">Paris</MenuItem>
                                    <MenuItem value="Falabella">Falabella</MenuItem>
                                    {/* Agrega ms tiendas aqu */}
                                </Select>
                            </FormControl>
        
                            {/* Filtro de Categoria */}
                            <FormControl variant="outlined" fullWidth sx={{ mb: 2 }}>
                                <InputLabel>Categora</InputLabel>
                                <Select value={categoryFilter} onChange={handleCategoryChange} label="Categora">
                                <MenuItem value="">Ninguno</MenuItem> {/* Aade una opcin vaca */}
                                    <MenuItem value="Frenos">Frenos</MenuItem>
                                    <MenuItem value="Motor">Motor</MenuItem>
                                    <MenuItem value="Elctrico">Elctrico</MenuItem>
                                    <MenuItem value="Suspensin">Suspensin</MenuItem>
                                    <MenuItem value="Transmisin">Transmisin</MenuItem>
                                    <MenuItem value="Carrocera">Carrocera</MenuItem>
                                    <MenuItem value="Refrigeracin">Refrigeracin</MenuItem>
                                    <MenuItem value="Escape">Escape</MenuItem>


                                </Select>
                            </FormControl>


        
                        {/* Botn de comparar o mostrar comparacin */}
                            {compareMode ? (
                                <Button
                                    variant="contained"
                                    color="secondary"
                                    fullWidth
                                    sx={{ mt: 2 }}
                                    onClick={handleShowComparison}
                                    disabled={selectedProducts.length < 2} // Habilita el botn si hay al menos 2 productos seleccionados
                                >
                                    Mostrar Comparacin
                                </Button>
                            ) : (
                                <Button
                                    variant="contained"
                                    color="primary"
                                    fullWidth
                                    sx={{ mt: 2 }}
                                    onClick={handleCompareToggle}
                                >
                                    Comparar productos
                                </Button>
                            )}

                            {/* Enlace para cancelar comparacin */}
                            {compareMode && (
                                <Typography
                                    variant="body2"
                                    color="primary"
                                    sx={{ mt: 1, cursor: 'pointer', textDecoration: 'underline', textAlign: 'center' }}
                                    onClick={handleCompareToggle}
                                >
                                    Cancelar comparacin
                                </Typography>
                            )}



                        </Paper>
                        </Collapse>
                        </Box>
                    </Grid>
        
                    {/* Columna de productos y ordenacin */}
                    <Grid item xs={12} md={9}>      
                        {errorMessage ? (
                            <Box sx={{ mt: 4, textAlign: 'center' }}>
                                <Typography variant="h6" color="error">
                                    {errorMessage}
                                </Typography>
                            </Box>
                        ) : (
                        <>
                        {/* Listado de productos */}
                        <Grid container spacing={3}>
                            {paginatedProducts.map((producto) => (
                                <Grid item xs={12} sm={6} md={4} lg={3} key={producto._id}>
                                    <Card
                                    sx={{
                                        bgcolor: '#f9f9f9',
                                        maxWidth: 300,
                                        boxShadow: selectedProducts.includes(producto) ? '0 0 0 3px #1976d2' : 3, // Contorno azul si est seleccionado
                                        position: 'relative', // Necesario para posicionar el checkbox

                                        width: '100%',
                                        height: '100%',
                                        display: 'flex',
                                        flexDirection: 'column',
                                        justifyContent: 'space-between',
                                        boxShadow: 3,
                                        cursor: 'pointer',
                                        transition: 'transform 0.2s ease, box-shadow 0.2s ease',
                                        '&:hover': {
                                            transform: 'scale(1.05)', // Mantiene el efecto de escala solo en hover
                                        }
                                    }}
                                        onClick={() => !compareMode && navigate(`/product/${producto._id}`)}
                                    >
                                    {compareMode && (
                                            <Checkbox
                                                checked={selectedProducts.includes(producto)}
                                                onChange={() => handleSelectProduct(producto)}
                                                color="primary"
                                                inputProps={{ 'aria-label': 'select to compare' }}
                                                sx={{
                                                    position: 'absolute',
                                                    top: 8,
                                                    right: 8,
                                                    bgcolor: 'rgba(255, 255, 255, 0.8)', // Fondo blanco semitransparente
                                                    borderRadius: '50%', // Checkbox circular
                                                    boxShadow: 1, // Sombra para destacar el checkbox
                                                    '&.Mui-checked': {
                                                        color: '#1976d2', // Color al estar seleccionado
                                                    },
                                                }}
                                            />
                                        )}
                                        <CardMedia
                                            component="img"
                                            height="200"
                                            width="200"
                                            image={producto.imagenUrl || 'https://via.placeholder.com/200'}
                                            alt={producto.nombre}
                                            sx={{ objectFit: 'cover' }}
                                        />
                                        <CardContent>
                                            <Typography variant="subtitle1" gutterBottom sx={{ fontWeight: 'bold' }}>
                                                {producto.nombre}
                                            </Typography>
                                            <Typography variant="h6" sx={{ mt: 1, color: '#003366' }}>
                                                ${producto.precio_actual}
                                            </Typography>
                                            <Typography variant="body2" >
                                                Marca: {producto.marca}
                                            </Typography>
                                            <Typography variant="body2" >
                                                Categoria: {producto.categoria}
                                            </Typography>
                                        </CardContent>
                                    </Card>
                                </Grid>
                            ))}
                        </Grid>
        
                        <Box sx={{ display: 'flex', justifyContent: 'center', mt: 4 }}>
                            <Pagination
                                count={Math.ceil(productos.length / productsPerPage)}
                                page={page}
                                onChange={handlePageChange}
                                color="primary"
                            />
                        </Box>
                        </>
                        )}
                    </Grid>
                </Grid>
                
                {/* Modal de comparacin */}
                <Modal open={openModal} onClose={handleCloseModal}>
                    <Box sx={{
                        position: 'absolute', top: '50%', left: '50%', transform: 'translate(-50%, -50%)',
                        bgcolor: 'background.paper', borderRadius: 2, boxShadow: 24, p: 4, width: '80%', maxWidth: 800
                    }}>
                        <Typography variant="h6" gutterBottom>Comparacin de Productos</Typography>
                        <Grid container spacing={2} justifyContent="center">
                            {selectedProducts.map((producto, index) => (
                                <Grid item xs={12} sm={4} key={index} gutterBottom>
                                    <Card 
                                        sx={{ boxShadow: 3, p: 2, textAlign: 'center', height: '100%', cursor: 'pointer' }}
                                        onClick={() => navigate(`/product/${producto._id}`)}
                                    >   
                                        <Box
                                            sx={{
                                                height: 150, // Altura fija para el rea de la imagen
                                                display: 'flex',
                                                alignItems: 'center',
                                                justifyContent: 'center',
                                                marginBottom: 2,
                                                backgroundColor: '#f2f2f2', // Color de fondo para diferenciar el espacio reservado
                                            }}
                                        >
                                            {producto.imagenUrl ? (
                                                <CardMedia
                                                    component="img"
                                                    height="100%" // Ajusta la altura de la imagen dentro del contenedor
                                                    image={producto.imagenUrl}
                                                    alt={producto.nombre}
                                                    sx={{ objectFit: 'cover' }}
                                                />
                                            ) : (
                                                <Typography variant="body2" color="text.secondary">Imagen no disponible</Typography>
                                            )}
                                        </Box>
                                        <Typography variant="h6">{producto.nombre || "-"}</Typography>
                                        <Typography>Precio: {producto.precio_actual ? producto.precio_actual.toLocaleString('es-CL', { style: 'currency', currency: 'CLP' }) : "-"}</Typography>
                                        <Typography>Marca: {producto.marca || "-"}</Typography>
                                        <Typography>Descuento: {producto.descuento || "-"}</Typography>
                                        <Typography>Tienda: {producto.descuento || "-"}</Typography>
                                    </Card>
                                </Grid>
                            ))}
                        </Grid>
                        <Button onClick={handleCloseModal} variant="contained" color="secondary" sx={{ mt: 2 }}>
                            Cerrar
                        </Button>
                    </Box>
                </Modal>
            </Container>
    );
    
}

export default CategoryPage;
