// src/components/Navbar.js
import React, { useState } from 'react';
import { AppBar, Toolbar, Typography, IconButton, InputBase, Button, Box, Select, MenuItem } from '@mui/material';
import { Search as SearchIcon, Language as LanguageIcon, Person as PersonIcon } from '@mui/icons-material';
import { Link, useNavigate } from 'react-router-dom';
import Sidebar from './Sidebar';

function Navbar() {
    const [searchQuery, setSearchQuery] = useState('');
    const navigate = useNavigate();
    const [language, setLanguage] = useState('en');
    const categories = [
        { name: 'Auto Parts' },
        { name: 'Interior Accessories' },
        { name: 'Exterior Accessories' },
        { name: 'Truck and Towing' },
        { name: 'Tools' },
        { name: 'Chemicals, Oil & Wash' },
        { name: 'Performance' },
    ];

    const handleCategorySelect = (category) => {    
        console.log('Selected Category:', category);
        // Aqu puedes agregar la lgica para filtrar o mostrar los productos de la categora seleccionada
    };

    const handleSearch = (e) => {
        e.preventDefault();
        if (searchQuery.trim()) {
            navigate(`/search?q=${searchQuery.trim()}`);
        }
    };

    const handleLanguageChange = (event) => {
        setLanguage(event.target.value);
        // Aqu puedes implementar la lgica para cambiar el idioma de la pgina
    };

    // Nueva funcin para manejar el clic en el logo
    const handleLogoClick = () => {
        setSearchQuery('');  // Limpia la barra de bsqueda
        navigate('/');       // Redirige al inicio
    };

    return (
        <AppBar 
            position="static"     
            sx={{ 
                bgcolor: '#000', 
                padding: { xs: '10px', md: '15px 50px' }, // Ajustar padding para pantallas pequeas
            }}>
            
            <Toolbar     
            sx={{
                display: 'flex',
                flexDirection: { xs: 'column', md: 'row' }, // Alineacin vertical en pantallas pequeas

                justifyContent: 'space-between', // Distribuye los elementos a los lados y centro
                alignItems: 'center',
                maxWidth: '1400px', // Ancho mximo del navbar
                width: '100%',

                mx: 'auto', // Centra el Navbar horizontalmente con margen automtico
                flexWrap: 'wrap', // Permite que los elementos se acomoden en varias filas en dispositivos pequeos

                gap: { xs: 2, md: 4 }, // Espaciado entre elementos
                padding: { xs: '0 10px', md: '0 20px' }, // Ajusta el padding segn el tamao de la pantalla
            }}
                >
                <Sidebar categories={categories} onCategorySelect={handleCategorySelect} />

                {/* Logo y botn de aadir vehculo */}
                <Box sx={{ display: 'flex', alignItems: 'center', mb: { xs: 2, md: 0 } }}>
                            <Typography
                                variant="h5"
                                sx={{ 
                                    fontWeight: 'bold', 
                                    color: '#FFD700', 
                                    cursor: 'pointer',
                                    fontSize: { xs: '1.2rem', md: '1.5rem' }, // Ajusta el tamao del logo en pantallas pequeas
                                }}
                                onClick={handleLogoClick}  // Ejecuta handleLogoClick al hacer clic en el logo
                            >
                                AutosAnaltica
                            </Typography>
                        </Box>

                        {/* Barra de bsqueda */}
                        <Box
                            component="form"
                            onSubmit={handleSearch}
                            sx={{
                                display: 'flex',
                                alignItems: 'center',
                                bgcolor: 'white',
                                borderRadius: 1,
                                px: 2,
                                width: { xs: '100%', sm: '80%', md: '60%' }, // Ajustar ancho para dispositivos pequeos
                                maxWidth: '600px', // Mximo ancho permitido
                                mx: 'auto', // Centra la barra de bsqueda horizontalmente
                                boxShadow: 1,
                                mb: { xs: 2, md: 0 }, // Margen inferior en pantallas pequeas

                            }}
                        >
                            <InputBase
                                placeholder="Buscar productos"
                                inputProps={{ 'aria-label': 'search' }}
                                value={searchQuery}
                                onChange={(e) => setSearchQuery(e.target.value)}
                                sx={{ flex: 1 }}
                            />
                        </Box>

                        {/* Iconos y botones de sesin */}
                        <Box 
                            sx={{ 
                                display: 'flex', 
                                alignItems: 'center', 
                                gap: 2, 
                                mt: { xs: 1, md: 0 }, // Margen superior en pantallas pequeas
                            }}
                        >
                            <IconButton sx={{ color: 'white' }}>
                                <PersonIcon />
                            </IconButton>
                        </Box>


                {/* Logo y botn de aadir vehculo */}
                {/*<Box sx={{ display: 'flex', alignItems: 'center', mr: 'auto' }}>
                    <Typography
                        variant="h5"
                        sx={{ fontWeight: 'bold', color: '#FFD700', cursor: 'pointer' }}
                        onClick={handleLogoClick}  // Ejecuta handleLogoClick al hacer clic en el logo
                    >
                        AutosAnaltica
                    </Typography>
                </Box>

                {/* Barra de bsqueda 
                <Box
                    component="form"
                    onSubmit={handleSearch}
                    sx={{
                        display: 'flex',
                        alignItems: 'center',
                        bgcolor: 'white',
                        borderRadius: 1,
                        px: 2,
                        width: { xs: '100%', sm: '60%', md: '60%' },
                        maxWidth: '600px',
                        mx: 'auto', // Centra la barra de bsqueda en el navbar
                        boxShadow: 1,
                        mr: 30, // Ajusta este valor segn cunto quieras moverla hacia la izquierda

                    }}
                    >
                    <InputBase
                        placeholder="Buscar productos"
                        inputProps={{ 'aria-label': 'search' }}
                        value={searchQuery}
                        onChange={(e) => setSearchQuery(e.target.value)}
                        sx={{ flex: 1 }}
                    />




                </Box>

                {/* Iconos y botones de sesin 
                <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>

                    <IconButton sx={{ color: 'white' }}>
                        <PersonIcon />
                    </IconButton>
                </Box>*/}



            </Toolbar>
        </AppBar>
    );
}

export default Navbar;
